import React from 'react';

import Menu from './Menu';
import Hero from './Hero';

export default function Layout({children}) {    
  return (
    <>
        <Menu />     

        <div className="container">
          <div className="d-sm-none d-sm-block d-lg-none mb-4 bg-light">
              <p className="text-center text-secondary">Telefones: <br /> 87-9999-999 / 87-9999-9999</p>
          </div>
          <Hero />       
          {children}

        </div>
    </>
  );
}
